<template>
  <div class="page">
    <mt-header class="bar-nav aui-border-b" title="评测结果">
      <router-link :to="investUrl" slot="right">
        <mt-button>完成</mt-button>
      </router-link>
    </mt-header>
    <div class="rat-result">
      <div class="rat-1">
        <img src="../../../assets/images/me/icon_evalution_results_bsx.png" />
      </div>
      <div calss="rat-2">
        <p class="result-title">您的风险承受能力类型为</p>
        <p class="your-style">{{$route.query.levelName}}</p>
      </div>
      <div class="rat-promt">
        <p>评测结果并不具备全面性及承诺性</p>
        <p>理财有风险，投资需谨慎。</p>
      </div>
      <div class="rat-btn">
        <router-link :to="investUrl">
          <mt-button type="default" class="rat-btn-left">{{investBtn}}</mt-button>
        </router-link>
        <router-link :to="backUrl">
          <mt-button type="default" class="rat-btn-right">重新评测</mt-button>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        investBtn: '立即投资',
        investUrl: '/invest',
        backUrl: '/mine/safe/risk_rating'
      }
    },
    created(){
      if(this.$route.query.from){
        this.investBtn = '继续投资';
        this.investUrl = '/'+this.$route.query.from+'/' + this.$route.query.id + '?from=risk_result';
        this.backUrl = '/mine/safe/risk_rating?from=' + this.$route.query.from +'&id='+ this.$route.query.id
      }
    }
  }
</script>
<style scoped>
  .rat-1{
    margin-top: .55rem;
    text-align: center;
  }
  .rat-1 img{
    width: 1.5rem;
    height: 1.5rem;
  }
  .result-title{
    font-size: .15rem;
    color: #333;
    text-align: center;
    padding: .23rem 0 .17rem 0;
  }
  .your-style{
    font-size: .36rem;
    color: #333;
    text-align: center;
  }
  .rat-promt{
    margin-top: .35rem;
    margin-bottom: .35rem;
  }
  .rat-promt p{
    text-align: center;
    font-size: .13rem;
    color: #666;
    line-height: .2rem;
  }
  .rat-btn-left,.rat-btn-right{
    width: 40%;
    background: #fff;
    padding: 0;
  }
  .rat-btn-left{
    margin-left: 7%;
    box-shadow: 0 0 1px #F95A28;
    color: #F95A28;
  }
  .rat-btn-right{
    margin-left: 4%;
    box-shadow: 0 0 1px #999;
    color:#999;
  }
</style>



